import { Button, message, Popconfirm, Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { VersionItemResponse } from "../../../models/versionManageModel";
import { VersionManageService } from "../../../services/adminService";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import MyTitle from "../../core/Title";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";
import ManageVersionModifyComponent from "./ModifyComponent";

export default function ManageVersionIndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [typeOrderBy, setTypeOrderBy] = useState<boolean>(false);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);
    const column = [
        new TableColumn({ title: "STT", dataIndex: "stt", key: "stt", disabled: true }),
        new TableColumn({
            title: "Loại", dataIndex: "type", key: "type", render: (data: number, row: any) => {
                let color = "";
                let value = "";
                if (data === 1) {
                    color = "red";
                    value = "Frontend";
                }

                if (data === 2) {
                    color = "green";
                    value = "Backend";
                }

                if (data === 3) {
                    color = "blue";
                    value = "Database";
                }

                return (
                    <Tag color={color}>{value}</Tag>
                )
            }
        }),
        new TableColumn({ title: "Phiên bản", dataIndex: "versionStr", key: "version" }),
        new TableColumn({ title: "Tên", dataIndex: "name", key: "name" }),
        new TableColumn({ title: "Ghi chú", dataIndex: "message", key: "message" }),
        new TableColumn({
            title: "Ngày phát hành", dataIndex: "dateRelease", key: "dateRelease", render: (data: string, row: any) => {
                return (<UIFormatDateTimeVNComponent value={data} />);
            }
        }),

        new TableColumn({
            title: "Hành động", key: "action", disabled: true, render: (data: any, row: any) => {
                return (
                    <div>
                        <MyButton
                            type="link"
                            value="Chỉnh sửa"
                            onClick={() => {
                                handleClickEdit(row.key);
                            }} />
                        <Popconfirm
                            title="Bạn có muốn xóa phiên bản này không?"
                            onConfirm={() => {
                                handleDelete(row.key);
                            }}>
                            <Button type="link">Xóa</Button>
                        </Popconfirm>
                    </div>
                );
            }
        })
    ];

    const [data, setData] = useState([]);
    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        VersionManageService.GetList(paginationRequest, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            setData(data.data.data.pagedData.map((data: any, index: number) => {
                return new VersionItemResponse(data, index);
            }));

        }, undefined, () => {
            setIsLoading(false)
        })
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleOnClose() {
        setVisible(false);
    }

    function handleOnCloseEdit() {
        setVisibleEdit(false);
        setKeyEdit(undefined);
    }

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickEdit(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function handleDelete(key: string) {
        setIsLoading(true);
        VersionManageService.GetDetail(key, (data: any) => {
            let timestamp = new VersionItemResponse(data.data.data).timestamp;
            VersionManageService.Delete(key, timestamp, (data: any) => {
                message.success(data.data.message);
                loadData();
            }, () => {
                setIsLoading(false)
            })
        }, () => {
            setIsLoading(false)
        });
    }

    return (
        <div>
            <MyTitle name="Quản lí phiên bản" />
            <MyButton
                type="primary"
                value="Thêm mới"
                onClick={handleClickAdd} />

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loadData={loadData}
                loading={isLoading}
                typeOrderBy={typeOrderBy}
                setTypeOrderBy={setTypeOrderBy} />

            <ManageVersionModifyComponent
                visible={visible}
                onClose={handleOnClose}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />

            <ManageVersionModifyComponent
                visible={visibleEdit}
                onClose={handleOnCloseEdit}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                keyEdit={keyEdit} />
        </div>
    );
}
