import { Col, Drawer, Form, message, Row } from "antd";
import { useCallback, useEffect, useState } from 'react';
import { Fn } from "../../../helper/function";
import { VersionItemResponse } from "../../../models/versionManageModel";
import { VersionManageService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import './logVersion.css';
var moment = require('moment');

export default function ManageVersionModifyComponent(props: any) {
    const options = [
        {
            label: 'Frontend',
            key: 'fr',
            value: 1
        },
        {
            label: 'Backend',
            key: 'be',
            value: 2
        },
        {
            label: 'Database',
            key: 'db',
            value: 3
        }
    ]

    const [type, setType] = useState(1);
    const [majorVersion, setMajorVersion] = useState(0);
    const [minorVersion, setMinorVersion] = useState(0);
    const [pathVersion, setPathVersion] = useState(0);
    const [preReleaseVersion, setReReleaseVersion] = useState('');
    const [dateRelease, setDateRelease] = useState(null);
    const [name, setName] = useState('');
    const [_message, setMessage] = useState('');

    const { setIsLoading } = props;

    function handleClickButton() {
        props.setIsLoading(true);
        let body = {
            type: type,
            version: {
                major: majorVersion,
                minor: minorVersion,
                patch: pathVersion,
                preRelease: preReleaseVersion
            },
            name: name,
            message: _message,
            dateRelease: dateRelease
        };
        if (props.keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        VersionManageService.Add(body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.onClose();
            props.loadData();
        }, () => {
            props.setIsLoading(false);
        })
    }

    function edit(body: any) {
        VersionManageService.GetDetail(props.keyEdit, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            VersionManageService.Edit(props.keyEdit, body, (data: any) => {
                message.success(data.data.message);
                props.onClose();
                props.loadData();
            }, () => {
                props.setIsLoading(false)
            })
        }, () => {
            props.setIsLoading(false)
        })
    }

    const loadData = useCallback(() => {
        if (props.keyEdit !== undefined) {
            setIsLoading(true);
            VersionManageService.GetDetail(props.keyEdit, (data: any) => {
                let row = new VersionItemResponse(data.data.data);
                setType(row.type);
                setMajorVersion(row.version.major);
                setMinorVersion(row.version.minor);
                setPathVersion(row.version.patch);
                setReReleaseVersion(row.version.preRelease);
                setName(row.name);
                setMessage(row.message);
                setDateRelease(moment(row.dateRelease));
            }, undefined, () => {
                setIsLoading(false)
            })
        }
    }, [props.keyEdit, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    return (
        <Drawer
            open={props.visible}
            onClose={props.onClose}
            title={props.keyEdit !== undefined ? "Chỉnh sửa log version" : "Thêm log version mới"}>
            <Form layout="vertical">
                <MyFormItem.Select
                    label="Loại"
                    options={options}
                    value={type}
                    setValue={setType} />

                <Form.Item label="Phiên bản">
                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Major"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={majorVersion}
                        setValue={setMajorVersion} />

                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Minor"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={minorVersion}
                        setValue={setMinorVersion} />

                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Patch"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={pathVersion}
                        setValue={setPathVersion} />

                    <Row className="row-version">
                        <Col span={8}>
                            <span>PreRelease</span>
                        </Col>
                        <Col span={16}>
                            <MyFormItem.Input
                                value={preReleaseVersion}
                                setValue={setReReleaseVersion} />
                        </Col>
                    </Row>
                </Form.Item>

                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.DatePicker
                    label="Ngày phát hành"
                    value={dateRelease}
                    setValue={setDateRelease} />

                <MyFormItem.TextArea
                    label="Ghi chú"
                    value={_message}
                    setValue={setMessage} />

                <MyButton
                    onClick={handleClickButton}
                    htmlType="submit"
                    type="success"
                    value={props.keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />
            </Form>
        </Drawer>
    );
}